<template>
    <div class="user-box">
        <div>
            <div class="options">
            <div>用户名<input type="text" v-model="username"></div>
            <div>用户姓名<input type="text" v-model="name"></div>
            <div>公司名称<input type="text" v-model="companyName"></div>
            <div>角色
                <template>
                    <el-select v-model="roleId" placeholder="请选择">
                         <el-option
                            v-for="item in rolesList"
                            :key="item.id"
                            :label="item.roleName"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </template>
            </div>
            <div>注册时间
                <el-date-picker
                v-model="startDate"
                type="date"
                placeholder="开始日期">
                </el-date-picker>
                ——
                <el-date-picker
                v-model="endDate"
                type="date"
                placeholder="结束日期">
                </el-date-picker>
            </div>
            <div style="margin-right:20px;margin-left:20px;">
                账号状态
                <template>
                    <el-select v-model="status" placeholder="请选择">
                        <el-option
                        v-for="item in accountStatus"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </template>
            </div>
            <div>
                联系电话<input type="text" v-model="mobile">
            </div>
            <div class="button-box">
                <span style="background:rgb(96,152,226);" @click="getUserData(1)">查询</span>
                <span style="background:rgb(172,172,172)" @click="handelReset()">重置</span>
            </div>
        </div>
        <div class="state-box">
            <div class="operation">
                <span @click="disableOrEnable('disable')">停用</span>
                <span @click="disableOrEnable('enable')">启用</span>
                <span @click="disableOrEnable('disableDown')">禁止文件下载</span>
                <span @click="initializationPassword()">初始化密码</span>
                <span @click="centerDialogVisible = true">批量修改用户角色</span>
            </div>
            <div class="add-btn">
                <router-link :to="{name:'addAccountNumber',params:{ key:'add'}}" tag="span">新增账号</router-link>
            </div>
        </div>
        <!--批量修改角色弹框-->
        <div class="batch-modification-roles">
            <el-dialog
                title="(批量)修改用户角色"
                :visible.sync="centerDialogVisible"
                width="40%"
                center>
            <div>
                <span>选中用户</span>
                 <span v-if="this.selectRow.length == 0" style="color:red;">
                   未选择任何用户
                </span>
                <span v-else class="checked-user" v-for="(item,index) in selectRow" :key="index">
                    {{ item.username }}
                    <i @click="delCheckedUser()">X</i>
                </span>
               
            </div>
            <div>
                <span>用户角色</span>
                <el-select v-model="roleValue" placeholder="请选择"  @change="selectValue">
                    <el-option
                        v-for="item in rolesList"
                        :key="item.id"
                        :label="item.roleName"
                        :value="item.id">
                    </el-option>
                </el-select>
            </div>
            <div v-if="roleName !== '普通用户'">
                <span>角色有效期</span>
                <input type="text" v-model="getDays">天
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="centerDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="confirm()">确 定</el-button>
            </span>
        </el-dialog>
        </div>
        <div class="list-box">
            <div>
                 <el-table
                        ref="multipleTable"
                        :data="tableData"
                        tooltip-effect="dark"
                        style="width: 100%"
                        @selection-change="handleSelectionChange">
                    <el-table-column
                        type="selection"
                        width="40">
                    </el-table-column>
                    <el-table-column
                        prop="username"
                        label="用户名"
                        width="100">
                    </el-table-column>
                    <el-table-column
                        label="用户姓名"
                        align="center"
                        width="100">
                        <template slot-scope="scope">
                            <span v-if="scope.row.name">{{scope.row.name}}</span>
                            <span v-else>——</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="公司名称">
                         <template slot-scope="scope">
                            <span v-if="scope.row.companyName">{{scope.row.companyName}}</span>
                            <span v-else>——</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="联系电话">
                         <template slot-scope="scope">
                            <span v-if="scope.row.mobile">{{scope.row.mobile}}</span>
                            <span v-else>——</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="roleName"
                        align="center"
                        label="角色">
                    </el-table-column>
                    <el-table-column
                        prop="roleTime"
                        align="center"
                        label="角色有效期">
                    </el-table-column>
                    <el-table-column
                        prop="createDateTime"
                        align="center"
                        label="注册时间">
                    </el-table-column>
                    <el-table-column
                        prop="status"
                        align="center"
                        width="80"
                        label="账户状态">
                    </el-table-column>
                    <el-table-column
                        prop="address"
                        align="center"
                        label="操作">
                        <template slot-scope="scope">
                            <el-button @click="handleClickSee(scope.row)" type="text" size="small">查看</el-button>
                            <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
                            <el-button @click="handleClickDel(scope.row)" type="text" size="small">删除</el-button>

                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="paging-box">
                <div></div>
                <div>
                    <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="pageSize"
                        layout="total,prev, pager, next, jumper"
                        :total="total">
                    </el-pagination>
                </div>
            </div>
        </div>
        </div>

    </div>
</template>
<script>
import Axios from 'axios'
import { javaServiceUrl } from './../../util/constant.js'
export default {
    data(){
        return{
            username:"",//用户名
            name:"",//用户姓名
            companyName:"",//公司名称
            roleId:"",//角色
            role:"",//角色下拉框
            status1:'',
            status:"",//账户状态
            mobile:"",//电话
            startDate:'',//开始日期
            endDate:"",//结束日期
            createDateTime:"",//注册时间
            roleTime:"",//角色有效期
            pageSize:10,
            total:0,
            pageNum:1,//第几页
            currentPage:1,
            value:"",
            value6:'',
            rolesList:[],//用户角色
            userRoles:"",
            accountStatus: [{
                value: '0',
                label: '使用中'
            }, {
                value: '-1',
                label: '已停用'
            },{
                value:'1',
                label:"禁止媒体下载文件"
            }],//账户状态
            roleValue: '', //用户角色
            value1:'',
            options:[],
            tableData: [],
            centerDialogVisible: false,
            //勾选每条信息
            selectRow:[],
            roleName:"",//用户角色
            getDays:0,//获取天数
            userIds:"",//选中的用户id
            authorization:"",//请求头
            token:"",
        }

    },
    mounted(){
        // this.getUserData();
       
        this.getUserRoles();
       
    },
    created(){
        var storage = window.localStorage;
        this.authorization = storage.getItem("userId");
        this.token = storage.getItem("token");
        
        this.getUserData(1);

    },
    methods:{ 
        //获取用户角色
        selectValue(a) {
            let obj = {};
            obj = this.rolesList.find((item)=>{
                return item.id === a;
            })
            this.roleName = obj.roleName;
        },
        //批量修改用户角色
        confirm() {
            for(let i = 0;i < this.selectRow.length;i++){
                this.userIds += "userIds=" + this.selectRow[i].id + "&";
            }
            this.userIds = this.userIds.substring(0,this.userIds.lastIndexOf('&'))
             Axios({
                url: javaServiceUrl+ '/userPermission/updateUsersPermission?roleId='+ this.roleValue+"&"+ this.userIds + '&days=' + this.getDays,
                method: 'get',
                headers: {"Authorization":"user-" + this.authorization + "-" +this.token},     
                })
                .then(res=>{
                    this.$message({
                        type:'success',
                        message:"更新"+res.data.code+'条！',
                    })
                   this.getUserData(1);
                })
                .catch(err=>{
                    console.log(err)
                })
            this.centerDialogVisible = false;
            this.selectRow = [];
            this.roleValue = "";
            this.getDays = "";
            this.userIds = "";
        },  
        //删除选中用户
        delCheckedUser(index){
            this.selectRow.splice(index,1);
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        toggleSelection(rows) {
            if (rows) {
            rows.forEach(row => {
                this.$refs.multipleTable.toggleRowSelection(row);
            });
            } else {
            this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.selectRow = val;
        },
      
      //修改
        handleClick(row) {
            this.$router.push({
                name:'addAccountNumber',
                params: {
                    row:row,
                    key:"modify"
                }
            })
        },
        //查看
        handleClickSee(row){
            this.$router.push({
                name:'addAccountNumber',
                params: {
                    row:row,
                    key:"look"
                }
            })
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.pageNum = val;
            this.getUserData(val);
        },
        //删除
        handleClickDel(scope,row){
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                Axios({
                url:javaServiceUrl+ '/userPermission/deleteWebUsers',
                    method: 'get',
                    headers:{"Authorization":"user-" + this.authorization + "-" +this.token}, 
                    params:{
                        webUserIds:scope.id
                    }
                })
                .then(res=>{
                    if(res.data.msg){
                        this.$router.push({
                            name:'login',
                        })
                    }else{
                        this.getUserData(1);
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
                    this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });

            
        },
      //停用 启用  
    disableOrEnable(type){
        var changeStatus = "";
        if(type === "disable"){
            changeStatus = -1;
        }else if(type === "enable"){
            changeStatus = 0;
        }else if(type === "disableDown"){
            changeStatus = 1;
        }
          for(let i = 0; i < this.selectRow.length; i++){
            //   if(this.selectRow[i].status == "使用中"){
            //         this.status = '0'
            //   }else if(his.selectRow[i].status == "已停用"){
            //         this.status = '-1'
            //   }else if(his.selectRow[i].status == "禁止媒体文件下载"){
            //         this.status = '1'
            //   }
            // console.log(this.selectRow)
            
            Axios({
                url: javaServiceUrl + '/userPermission/updateWebUserStatus',
                method: 'get',
                headers: {"Authorization":"user-" + this.authorization + "-" +this.token},    
                params:{
                    id:this.selectRow[i].id,
                    status:changeStatus
                }
                })
                .then(res=>{
                    if(res.status == 200){
                        this.$message({
                            type:'success',
                            message:"更新"+res.data.code+'条！',
                        })
                    this.getUserData(1);
                    }else if(res.status == 500){
                        this.$alert(res.message,'提示',{
                            confirmButtonText:'确定',
                            type:'warning'
                        })
                    }else{
                        this.$message.error("修改失败，请稍后再试！")
                    }
                    return;
                })
                .catch(err=>{
                    console.log(err)
                })
          }
      },
      //用户角色
        getUserRoles(){
            Axios({
               url:javaServiceUrl + '/userPermission/findRoles',
               method: 'get',
               headers: {"Authorization":"user-" + this.authorization + "-" +this.token},    
               params:{
                    roleName:"",
                    pageSize:1000,
                    pageNum:0
                }
            })
            .then(res=>{

                if(res.data.msg){
                    this.$router.push({
                        name:'login',
                    })
                }else{
                    this.userRolesTemp = res.data.list;

                    for( let i = 0;i < this.userRolesTemp.length; i++ ){
                        if(this.userRolesTemp[i].state != -2){
                            this.rolesList.push(this.userRolesTemp[i])
                        }
                    }
                }
            })
            .catch(err=>{
                console.log(err)
            })
        },
      //重置
      handelReset(){
          this.username = "";
          this.name = "";
          this.status = "";
          this.roleId = "";//角色ID
          this.mobile = "";
          this.companyName = "";
          this.startDate = "";
          this.endDate = "";
          this.status1 = ""; //账户状态
          this.role = "";//角色
          this.getUserData(1);
      },
      //初始化密码
      initializationPassword(){
          for(let i = 0; i < this.selectRow.length; i++){
            Axios({
                url: javaServiceUrl + '/userPermission/updateWebUserPassword',
                method: 'get',
                headers: {"Authorization":"user-" + this.authorization + "-" +this.token},    
                params:{
                    id:this.selectRow[i].id,
                   password:123456
                }
            })
            .then(res=>{
                if(res.data.msg){
                    this.$router.push({
                        name:'login',
                    })
                }else{
                    if(this.selectRow.length-1 == i){
                        this.$message({
                            message: "更新"+res.data.code+'条！',
                            type: 'success'
                        });
                    }
                }
                
                
            })
            .catch(err=>{
                console.log(err)
            })

          }
            
        // this.$confirm('确认初始化密码?', '提示', {
        //   confirmButtonText: '确定',
        //   cancelButtonText: '取消',
        //   type: 'warning',
        // }).then(()=>{
        //     this.selectRow.forEach(item => {
        //         item.password = "123456";
        //         console.log(item.password)
        //     })
        // })

    },
    //获取列表数据
    getUserData(pageNumber){
        // console.log({"Authorization":"user-" + this.authorization + "-" +this.token})
        Axios({
            url: javaServiceUrl + '/userPermission/findWebUsers',
            method: 'get',
            headers: {"Authorization":"user-" + this.authorization + "-" +this.token},     
            params:{
                    pageSize:this.pageSize,
                    //pageNum:this.pageNum - 1,
                    pageNum:pageNumber-1,
                    username:this.username,
                    name:this.name,
                    status:this.status,
                    roleId:this.roleId,
                    mobile:this.mobile,
                    companyName:this.companyName,
                    startDate:getNowFormatDate(this.startDate),
                    endDate:getNowFormatDate(this.endDate)
                }          
            })
            .then(res=>{
                if(res.data.msg){
                    this.$router.push({
                        name:'login',
                    })
                }else{
                    this.tableData = res.data.list;
                    this.total = res.data.count;
                    for(var i = 0;i < this.tableData.length; i ++){
                        this.tableData[i].createDateTime = this.tableData[i].createDateTime.substring(0,10);
                        this.tableData[i].roleTime = this.tableData[i].roleTime.substring(0,10);
                        // this.tableData[i].roleTime = getNowFormatDate(this.tableData[i].roleTime);
                        if(this.tableData[i].status == 0){
                            this.tableData[i].status = "使用中";
                        }else if(this.tableData[i].status == -1){
                            this.tableData[i].status = "已停用";
                        }else if(this.tableData[i].status == 1){
                            this.tableData[i].status = "禁止媒体下载文件";
                        }
                    }
                }
            })
            .catch(err=>{
                console.log(err)
            })
    },

    /*删除数组中的某一个对象
    _arr:数组
    _obj:需删除的对象
    */
    removeAaary(_arr, _obj) {
        var length = _arr.length;
        for (var i = 0; i < length; i++) {
            if (_arr[i] == _obj) {
                if (i == 0) {
                    _arr.shift(); //删除并返回数组的第一个元素
                    return _arr;
                }
                else if (i == length - 1) {
                    _arr.pop();  //删除并返回数组的最后一个元素
                    return _arr;
                }
                else {
                    _arr.splice(i, 1); //删除下标为i的元素
                    return _arr;
                }
            }
        }
    }

    },
    watch:{
        '$route'(to,from){
            console.log('to',to)
            console.log('from',from)
        }
    }
}
//  function getNowFormatDate (time) {
//   const dateee = new Date(time).toJSON()
//   return new Date(+new Date(dateee) + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
// }
function getNowFormatDate(e) {
    if(e == ""){
        return ;
    }
    var date = new Date(e);
    var seperator1 = "-";
    var month = (date.getMonth() + 1).toString().padStart(2,'0');
    var strDate = date.getDate().toString().padStart(2,'0');
    var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate 
    return currentdate.substring(0,10);
}
</script>
<style lang='scss' scoped>
    .user-box{
       
        .options{
            font-size: 16px;
           height: 158px;
           background: #fff;
           border-radius: 8px;
           padding: 15px 20px;
           display: flex;
           flex-wrap: wrap;
           div{
               line-height: 40px;
               input{
                width: 166px;
                height: 40px;
                border-radius: 4px;
                border: 1px solid #DCDFE6;
                margin-right: 20px;
                margin-left: 5px;
               }
               
           }
        }
        .el-date-editor--daterange.el-input, .el-date-editor--daterange.el-input__inner, .el-date-editor--timerange.el-input, .el-date-editor--timerange.el-input__inner{
            width: 230px;
            font-size: 12px;
            margin-left: 5px;
        }
        .button-box{
            span{
                display: inline-block;
                width: 66px;
                height: 26px;
                text-align: center;
                line-height: 26px;
                font-size: 14px;
                border-radius: 13px;
                color: #fff;
                cursor: pointer;
            }
        }
        .state-box{
            line-height: 60px;
            display: flex;
            justify-content: space-between;
            font-size: 14px;
            .operation{
                margin-left: 20px;
                span{
                    color: rgb(79,150,244);
                    margin-right: 15px;
                    cursor: pointer;
                }
            }
            .add-btn{
                margin-right: 20px;
                span{
                    display: inline-block;
                    width: 89px;
                    height: 30px;
                    line-height: 30px;
                    text-align: center;
                    background: rgb(241,145,73);
                    border-radius: 15px;
                    cursor: pointer;
                    color: #fff;
                }
            }
        }
        .list-box{
            .paging-box{
                display: flex;
                justify-content: space-between;
                margin-top: 15px;
            }
        }
        .Popup{
            width: 100%;
            height: 100%;
            background:rgba(0,0,0,0.8);
            position: absolute;
            top: 0;
            left: 0;
            z-index: 900;
        }
        .accountNumber{
            width: 500px;
            height: 220px;
            background: #fff;
        }
        .batch-modification-roles{
            input{
                width: 200px;
                height: 40px;
                border: 1px solid #DCDFE6;
                border-radius: 4px;
            }
            .checked-user{
                display: inline-block;
                padding: 5px 10px;
                background: #409EFF;
                border-radius: 4px;
                color: #fff;
                position: relative;
                margin-right: 10px;
                margin-bottom: 10px;
                i{
                    display: inline-block;
                    width: 15px; 
                    height: 15px;
                    line-height: 15px;
                    text-align: center;
                    background: #fff;
                    border-radius: 50%;
                    color: #999;
                    position: absolute;
                    top: -5px;
                    right: -4px;
                    cursor: pointer;
                }
            }
            div{
                margin: 10px 0;
            }
        }
    }
</style>



